$bgColor: #fff;
$text: #333;

@font-face {
  font-family: 'TT Lakes', sans-serif;
  src: url('https://fontsfree.pro/base-web-fonts/sans-serif-grotesque/897-tt-lakes.html') format('truetype');
  font-weight: normal;
  font-style: italic;
}

body {
  padding: 1rem;
  margin: 0;
  font-family: 'TT Lakes', sans-serif;
  background-color: $bgColor;
  color: $text;
  line-height: 1.5;
}

.sidebar {
  width: 320px;
  height: 100vh;
  background: #fff;
  box-shadow: 4px 0 16px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 16px;
}

/* header */
.sidebar__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /*is it because of this that I can't put the logo next to Xmenu icon?*/
}

.sidebar__icon {
  background-color: #FF3E79;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  
}

.sidebar__logo img {
  height: 32px;
  /*I want to put it next to the Xmenu button but can't seem to find how*/
}

/*Also why is the menu not sticking to the header?*/

/* Menu */
.sidebar__menu {
  display: flex;
  flex-direction: column;
  gap: 14px;
  margin-top: 0%;
  margin-left: 23px;
}

.marker {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    opacity: 0.7;
  filter: brightness(1.1);
}

.highlight {
 height: 28px;
 width: 4px; 
 padding-right: 2p;
}

.sidebar__menu-item {
  border: none;
  background: none;
  font-size: 16px;
  text-align: left;
  color: #1B1C21;
  cursor: pointer;
  left: 23px;
}

.sidebar__menu-item:hover{
  font-weight: 700;
  text-shadow: 1px 2px 4px gray;

}


.space {
   height: 70px;
}

/*footer*/
.sidebar__bottom {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.sidebar__icons {
  display: flex;
  gap: 12px;
}

.sidebar__icons button {
  background-color: #FF3E79;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: 0.3s ease;
}

.sidebar__mail {
  font-style: medium;
  font-size: 16px;
}

.sidebar__phone { 
   font-style: bold;
  font-size: 25px;
  font-weight: 700;
}

.sidebar__lang {
  display: flex;
  gap: 10px;
}

.sidebar__lang a {
  text-decoration: none;
  color: #1B1C21;
  opacity: 0.7;
  transition: 0.3s ease;
}

.sidebar__lang a:hover {
  opacity: 0.5;
}
 
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
}

.header__logo img {
  height: 40px;
}

.header__icons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.header__icon {
  background-color: #FF3E79;
  border-radius: 60%;
  border: #FF3E79;
  width: 40px;
  height: 40px;
  cursor: pointer;
  margin-left: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__icon img {
  width: 22px;
  height: 22px;
}

.main {
  padding: 16px;
}

.services__title {
  font-size: 1.3rem;
  font-weight: 700;
  margin-top: 3px;
}

.services__menu {
  display: flex;
  overflow-x: auto;
  gap: 8px;
  margin-bottom: 1.2rem;
}

.services__menu-item {
  border: none;
  border-radius: 6px;
  background: none;
  padding: 6px 12px;
  white-space: nowrap;
  cursor: pointer;
  flex-shrink: 0;
  justify-content: flex-start;
}

.services__menu-item:hover {
  border:  2px solid #B8FFEC;
}
.services__menu-item::selection {
   border:  2px solid #B8FFEC;
}  /* not sure this does anything?*/

.services__content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.services__text p {
  margin-bottom: 0.7rem;
}

.services__readmore {
  display: inline-block;
  color: #1B1C21;
  font-weight: 700;
  text-decoration: none;
  margin-top: 0.5rem;
}

.services__image img {
  width: 100%;
  border-radius: 8px;
  display: block;
}

.services__header-buttons {
  display: none;
}

 /*mobile*/
@media (max-width: 320px){
 .header__icon--call {
  display: none;
 }

 .header__icon--chat {
  display: none;
 }

 .header__icon--profile {
  display: none;
 }

 .services__menu-item {
  width: 246;
height: 40;
top: 176px;
left: 16px;
opacity: 1;
border-radius: 6px;
border-width: 2px;

 }

 .marker {
  width: 306px;
 }

 p {
  width: 288;
height: 72;
top: 240px;
left: 16px;
opacity: 1;
 }


 }


/* TABLET / DESKTOP */
@media (max-width: 768px) {
  .header {
    padding: 12px 40px;
  }

  .header__icon--call {
  display: none;
 }

 .header__icon--chat {
  display: none;
 }

 .header__icon--profile {
  display: none;
 }

  .header__icon--request {
    display: inline-block; 
  }

  .header__logo img {
    height: 36px;
  }

  .services__content {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
  }

  
.services__header-buttons {
  display: none;
}

  .services__text {
    flex: 1;
  }

  .services__image {
    flex: 1;
  }

  .services__title {
    font-size: 1.8rem;
  }

 .divider {
  width: 1px;
  height: 32px;
  color: lightgray;
  border: none;
 }

  .services__header-buttons {
    display: none;
  }
}

/* LARGE SCREEN */
@media (min-width: 1120px) {
  body {
    max-width: 1200px;
    margin: 0 auto;
  }

  .header {
    display: none;
  }

  .services__header {
   display: flex;
   justify-content: space-between;
   align-items: center;
  }

  .services__header-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .services__header-buttons-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

   .header__icon--menu {
    display: none; 
  }

   .header__icon--call {
  display: none;
 }

 .header__icon--chat {
  display: none;
 }

 .header__icon--profile {
  display: none;
 }
  .services__title {
    font-size: 2rem;
  }

  .services__text p {
    font-size: 14px;
    width: 490;
height: 160;
top: 188.22px;
left: 35px;
opacity: 1;

  }

  .services__menu-item {
    font-size: 1rem;
    padding: 8px 16px;
  }



  .services__image img {
    max-width: 500px;
  }
}


.title {
  height: 24;
  opacity: 0.5;
  padding-left: 32px;
  padding-top: 30px;
  font-size: 13px;
  background: linear-gradient(#f8f8f8, #ffffff);
}

.brands-block {
  padding: 24px 32px;
}

.container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  padding-left: 32px;
  padding-top: 30px;
  gap: 10px;
  width: 224px;
  height: 72px;
  transition: max-height 0.3s ease;
}

.container.expanded {
  max-height: 2000px;
}

button {
  width: 240px; 
  max-width: 240px; 
  height: 72px; 
  background-color: white;
  border: 1px solid #eaeaea;
  border-radius: 6px; 
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  object-fit: contain; 
}


.go {
  height: 20px;
  margin: 6px;
  align-items: center;
  cursor: pointer;
}

.toggle {
  display: none;

}

.arrow {
  transition: transform 0.3s;
}

.arrow.rotated {
  transform: rotate(180deg);
}


/* Container size */
.brands-grid {
  display: none;
}

.brands-swiper {
  width: 100%;
  max-width: 320px;
  height: 300px; 
  margin: 0 auto;
}

/* Slide styling */
.swiper-slide {
  color: #fff;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
}

/* Optional: pagination dots */
.swiper-pagination-bullet {
  background: #000;
}

@media (min-width: 768px) {
  .brands-swiper {
    display: none;
  }

  .brands-block {
    max-width: 1050px;
  }
  .brands-grid {
    display: flex;
    flex-wrap: wrap; /* allows items to move to next row */
    gap: 10px;
  }

  .brands-grid button {
    width: calc((100% - 2 * 10px) / 3); /* 3 columns with gap */
  }

  .toggle {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-top: 16px;
    cursor: pointer;
    font-size: 14px;
  }

  .collapsed-tablet {
    display: none;
  }
  .brands-grid.expanded .collapsed-tablet {
    display: flex;
  }
}

@media (min-width: 1120px) {
  .brands-swiper {
    display: none;
  }

  .brands-grid {
    width: 1000px;
    display: flex;
    flex-wrap: wrap; /* allows items to move to next row */
    gap: 10px;
  }

  .brands-grid button {
    width: calc((100% - 3 * 10px) / 4); /* 4 columns with gap */
  }

  .toggle {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-top: 16px;
    cursor: pointer;
    font-size: 14px;
  }
  .collapsed-tablet {
    display: none;
  }
  .collapsed-pc {
    display: flex;
  }
  .brands-grid.expanded .collapsed-pc {
    display: none;
  }
}


